import React, {useContext, useEffect} from 'react';
import './CoverImage.component.scss';
import ScrollDownIndicator from '../../components/ScrollDownIndicator/ScrollDownIndicator.component.jsx';
import SectionH1 from '../SectionH1/Sectionh1.component';
import { motion, AnimatePresence, useAnimation } from 'framer-motion';
import { AppContext } from '../../AppContext';

export default function CoverImage({ image, title, currentPos}) {
    const { isMD, isXXL } = useContext(AppContext);
    const animateTitle = useAnimation();
    useEffect(() => {
        animateTitle.set({
            opacity: 0
        })
        animateTitle.start({
            opacity: 1,
            transition: { 
                delay: 0.5, 
                duration: 0.5, 
                ease: 'easeIn'
            }
        })
    }, [animateTitle])
    
    return (
        <AnimatePresence>
            <div className='cover-image full-vh' style={{opacity: currentPos}}>
                <motion.div className="overlay" id="ci-overlay"></motion.div>
                <motion.img 
                    src={image}
                    alt={title}
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    exit={{ opacity: 0 }}
                    transition={{
                        opacity: { duration: 1 },
                    }}
                />
                <SectionH1
                    animation={animateTitle} 
                    classe={isXXL ? `display1 title` : isMD ? `display2 title` : `title`}
                    h1={title}
                />
                <ScrollDownIndicator/>
            </div>
        </AnimatePresence>
    )
}
