import React, { useContext, useState, useEffect, Fragment } from 'react';
import './PortfolioItem.component.scss';
import Layout from '../../components/Layout/Layout.component';
import { useLocation } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import { AppContext } from '../../AppContext';
import CoverImage from '../../components/CoverImage/CoverImage.component.jsx';
import Wrapper from '../../components/Wrapper/Wrapper.component.jsx';
import Container from '../../components/Container/Container.component.jsx';  
import Markdown from 'markdown-to-jsx';
import SectionH2 from '../../components/SectionH2/Sectionh2.component';
import SectionH1 from '../../components/SectionH1/Sectionh1.component';
import useFadeOutHook from '../../hooks/FadeOutHook/FadeOutHook.component.jsx';
import Footer from '../../components/Footer/Footer.component.jsx';

export default function PortfolioItem() {

  const location = useLocation();
  const pathname = location.pathname;
  const { portfolioData } = useContext(AppContext);
  const [text, setText] = useState('');
  const currentPos = useFadeOutHook()

  useEffect(() => {

    portfolioData.forEach( data => {
      if(`/${data.link}` === pathname){
        import(`../../data/markdown/${data.markdown}`)
        .then((res) => {
          fetch(res.default)
            .then((res) => res.text())
            .then((res) => setText(res))
            .catch((err) => console.log(err));
        })
        .catch((err) => console.log(err));
      }
    });

  }, [pathname, portfolioData]);


  return (
    <Layout>
      {
        portfolioData.map( data => (
          `/${data.link}` === pathname &&
          <Fragment key={data.id} >
            <Helmet>
              <title>{data.title}</title>
              <meta
                name="description"
                content={`ISR Technology - USA, MIAMI, FL. ${data.meta}`}
              />
            </Helmet>
            <CoverImage image={process.env.REACT_APP_PUBLIC_URL + data.image} title={data.title} currentPos={currentPos}/>
            <Wrapper>
              <Container 
                display={`flex`} 
                maxWidth={`768px`}
                classe={`services-container`}
              >
                <SectionH1 h1={data.title} />
                <div className="underline"></div>
                <div>
                  <Markdown>
                    {text}
                  </Markdown>
                </div>
              </Container>
            </Wrapper>
          </Fragment>
        )
      )}
      <Footer/>
    </Layout>
  );
}